
.inner-bg {
        margin-top: 50px;
}
.element-center{
        margin:0 auto;
        width:45%
}
.form-top {
        overflow: hidden;
        padding: 0 25px 15px 25px;
        background: #fff;
        border-radius: 4px 4px 0 0;
        text-align: left;
}
.form-bottom {
        padding: 25px 25px 30px 25px;
        background: #eee;
        border-radius: 0 0 4px 4px;
        text-align: left;
}
.form-top-left {
        float: left;
        width: 75%;
        padding-top: 25px;
}
.form-top-right {
        float: left;
        width: 25%;
        padding-top: 5px;
        font-size: 66px;
        color: #ddd;
        line-height: 100px;
        text-align: right;
}
.error{
        color:red;
}
.text-center{
        text-align:center
}
.login-form input[type="password"],
.login-form input[type="text"],
.login-form input[type="email"],
.login-form input[type="number"],
.login-form textarea,
.login-form textarea.form-control {
	height: 50px;
    margin: 0;
    padding: 0 20px;
    vertical-align: middle;
    background: #f8f8f8;
    border: 3px solid #ddd;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 50px;
    color: #888; border-radius: 4px; transition: all .3s;
}
.login-form button.btn {
	height: 50px;
    margin: 0;
    padding: 0 20px;
    vertical-align: middle;
    background: #4aaf51;
    border: 0;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 50px;
    color: #fff; border-radius: 4px;
    text-shadow: none; box-shadow: none; transition: all .3s;
}
.login-form button.btn:hover { opacity: 0.6; color: #fff;
}
.login-form button.btn:active { outline: 0; opacity: 0.6; color: #fff; box-shadow: none;
}
.login-form button.btn:focus { outline: 0; opacity: 0.6; background: #4aaf51; color: #fff;
}
.login-form button.btn:active:focus, button.btn.active:focus { outline: 0; opacity: 0.6; background: #4aaf51; color: #fff;
}




